import { toCancelOrderApi, toPayOrderApi, toConfirmReceiptApi } from '@/api/orderList'

export default {
  methods: {
    // 取消订单
    toCancelOrder (orderId) {
      this.$dialog.confirm({
        message: '确定要取消订单吗'
      }).then(async () => {
        await toCancelOrderApi(orderId)
        this.$router.go(0)
      }).catch(() => {
      })
    },

    // 支付订单
    async toPayOrder (orderId, payType) {
      await toPayOrderApi(orderId, payType)
      this.$toast('支付成功')
      this.$router.go(0)
    },

    // 确认收货
    toConfirmReceipt (orderId) {
      this.$dialog.confirm({
        message: '您确定已经收到货了吗'
      }).then(async () => {
        await toConfirmReceiptApi(orderId)
        this.$toast('确认收货成功')
        this.$router.go(0)
      }).catch(() => {})
    }
  }
}
