<template>
  <van-action-sheet title="支付订单" closeable round :value="isPayAction"
  @click-overlay="sendClose" @cancel="sendClose">
    <van-radio-group v-model="payType" checked-color="#ee0a24">
      <van-cell-group>
        <van-cell clickable @click="payType = '10'">
          <template #title>
            <van-icon name="balance-o" color="#ff9700"></van-icon>
            <span>余额支付</span>
          </template>
          <template #right-icon>
            <van-radio name="10" />
          </template>
        </van-cell>

        <van-cell clickable @click="payType = '0'">
          <template #title>
            <van-icon name="info-o" color="#ee0a24"></van-icon>
            <span>微信支付</span>
          </template>
          <template #right-icon>
            <van-radio name="0" />
          </template>
        </van-cell>
      </van-cell-group>
    </van-radio-group>

    <div class="btn now" @click="toPayOrder(orderData.order_id, +payType)">立即付款</div>
  </van-action-sheet>
</template>

<script>
import orderActions from '@/mixins/orderActions'

export default {
  name: 'PayActionSheet',
  mixins: [orderActions],
  props: {
    isPayAction: {
      required: true,
      type: Boolean
    },

    orderData: {
      type: Object
    }
  },

  data () {
    return {
      payType: '10'
    }
  },

  methods: {
    sendClose () {
      this.$emit('closeSheet', false)
    }
  }

}
</script>

<style lang="less" scoped>
.btn {
  height: 40px;
  line-height: 40px;
  margin: 20px;
  border-radius: 20px;
  text-align: center;
  color: rgb(255, 255, 255);
  background-color: rgb(255, 148, 2);
}
.btn.now {
  background-color: #fe5630;
}
</style>
