import requestUtil from '@/utils/request'

// 获取订单列表
export const getOrderListApi = (dataType, page) => {
  return requestUtil({
    url: '/order/list',
    params: {
      dataType,
      page
    }
  })
}

// 我的-取消订单
export const toCancelOrderApi = (orderId) => {
  return requestUtil({
    method: 'post',
    url: '/order/cancel',
    data: {
      orderId
    }
  })
}

// 我的-订单支付
export const toPayOrderApi = (orderId, payType) => {
  return requestUtil({
    url: '/order/pay',
    params: {
      orderId,
      payType
    }
  })
}

// 我的-订单确认收货
export const toConfirmReceiptApi = (orderId) => {
  return requestUtil({
    method: 'post',
    url: '/order/receipt',
    data: {
      orderId
    }
  })
}

// 订单评价-获取商品列表
export const getGoodsListApi = (orderId) => {
  return requestUtil({
    url: '/order.comment/list',
    params: {
      orderId
    }
  })
}

// 上传图片
export const uploadImageApi = (formData) => {
  return requestUtil({
    method: 'post',
    url: '/upload/image',
    data: formData
  })
}

// 提交商品评价
export const submitCommentApi = (orderId, form) => {
  return requestUtil({
    method: 'post',
    url: '/order.comment/submit',
    data: {
      orderId,
      form
    }
  })
}

// 获取订单详情
export const getOrderDetailApi = (orderId) => {
  return requestUtil({
    url: '/order/detail',
    params: {
      orderId
    }
  })
}
