<template>
  <div class="order">
    <van-nav-bar fixed title="我的订单" left-arrow @click-left="$router.back()">
    </van-nav-bar>

    <van-tabs v-model="activeName" @click="changeTab" sticky>
      <van-tab title="全部" name="all"></van-tab>
      <van-tab title="待支付" name="payment"></van-tab>
      <van-tab title="待发货" name="delivery"></van-tab>
      <van-tab title="待收货" name="received"></van-tab>
      <van-tab title="待评价" name="comment"></van-tab>
    </van-tabs>

    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="loadOrder"
    >
      <OrderListItem
        v-for="(item, index) in orderList"
        :key="index"
        :everyOrder="item"
      ></OrderListItem>
    </van-list>
  </div>
</template>

<script>
import OrderListItem from '@/components/OrderListItem.vue'
import { getOrderListApi } from '@/api/orderList'

export default {
  name: 'OrderIndex',
  components: {
    OrderListItem
  },

  data () {
    return {
      page: 1,
      activeName: this.$route.query.dataType || 'all',

      loading: false,
      finished: false,

      orderList: []
    }
  },

  computed: {

  },

  methods: {
    // 切换标签
    async changeTab (name) {
      this.activeName = name
      this.page = 1
      this.loading = true
      this.finished = false
      this.orderList = []
      await this.loadOrder()
      this.$router.replace(`myorder?dataType=${name}`)
    },

    // 加载
    async loadOrder () {
      await this.getOrderList()
    },

    // 获取订单
    async getOrderList () {
      const { data: { list } } = await getOrderListApi(this.activeName, this.page)
      // console.log(list)
      this.orderList = this.orderList.concat(list.data)
      this.loading = false
      this.page++

      if (this.orderList.length >= list.total) {
        this.finished = true
      }
    }

  },

  created () {
  }
}
</script>

<style lang="less" scoped>
.order {
  background-color: #fafafa;
  padding-top: 48px;
}
.van-tabs {
  position: sticky;
  top: 46px;
  z-index: 999;
}
</style>
